
@charset "UTF-8";
.experts {
  min-height: calc(100% - 80px);
  height: calc(100% - 80px);
}
.experts-content {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 200px;
}
.experts-content > div {
  display: inline-block;
  vertical-align: top;
}
.experts .content-left {
  /*width:18% ;*/
  /* padding-top: 80px; */
  height: 100%;
  border-right: 1px solid #eee;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.experts .content-right {
  width: 100%;
  /* padding-top: 30px; */
}
.content-right .right_header {
  width: 100%;
  padding: 10px 0;
  background-color: #f8f8f8;
  overflow: hidden;
}
.content-right .right_header .header_content {
  width: 99%;
  margin: 0 auto;
}
.header_content .province {
  float: left;
  width: 10%;
  height: 45px;
  text-align: right;
  line-height: 45px;
  border-radius: 5px;
}
.header_content .province.on span {
  color: #3C9BFF;
}
.header_content .province span {
  color: #333;
  font-size: 1.5vw;
  vertical-align: middle;
}
.header_content .province img {
  vertical-align: middle;
  margin-right: 2%;
}
.header_content .hospital_wrap {
  float: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin-left: 1%;
  width: 88%;
}
.header_content .hospital {
  height: 45px;
  line-height: 45px;
  width: 33%;
  position: relative;
}
.header_content .hospital span {
  font-size: 1.5vw;
  color: #333;
  float: left;
  line-height: 42px;
  height: 42px;
  margin-top: 2px;
  display: inline-block;
  width: 0.6rem;
  text-align: center;
}
.header_content .hospital img {
  position: absolute;
  right: 7%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 17px;
}
.header_content .hospital ul,
.header_content .hospital select {
  width: 75%;
  height: 45px;
  background-color: #FFFFFF;
  margin-left: 1%;
  border: 1px solid #3C9BFF;
  border-radius: 5px;
  font-size: 1.5vw;
  color: #3C9BFF;
  outline: none;
  padding-left: 3%;
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
li.right-item {
  padding: 10px 0;
  border-bottom: 1px solid #eee;
}
li.right-item > div {
  display: inline-block;
  vertical-align: middle;
}
.experts .right-item-left {
  width: 73%;
}
.experts .right-item-left img {
  width: auto;
  height: 80px;
  vertical-align: middle;
  margin-right: 10px;
  margin-left: 20px;
  border-radius: 50%;
}
.experts .detail {
  display: inline-block;
  vertical-align: middle;
  width: calc(100% - 120px);
  font-size: 1.5vw;
}
.experts .detail p:nth-of-type(1) {
  margin-bottom: 8px;
}
.experts .name {
  font-size: 1.8vw;
  font-weight: bold;
  margin-right: 12px;
}
.experts .right-item-right {
  width: 26%;
  text-align: right;
  font-size: 1.5vw;
}
.experts .right-item-right span {
  vertical-align: middle;
  display: inline-block;
  background: #3c9bff;
  color: #fff;
  border-radius: 5px;
  margin-right: 20px;
  width: 1.5rem;
  line-height: 2.3em;
  text-align: center;
  font-size: 0.28rem;
}
.experts .right-item-right span.full {
  background: #eee;
  color: #9b9b9b;
}
.experts .right-item-right span.danger {
  background: #FC9452;
  color: #fff;
}
.experts .left-item {
  text-align: center;
  line-height: 5.2vw;
  font-size: 0.32rem;
}
.experts .left-item.on {
  background: #3c9bff;
  color: #fff;
}
.right-item-right.full span {
  background: #eee;
  color: #999;
}
.right-item-right span {
  background: #eee;
  color: #999;
}
.experts-location {
  display: none;
  text-align: center;
  line-height: 5.2vw;
  font-size: 0.32rem;
}
.experts-location img {
  width: auto;
  height: 20px;
  vertical-align: middle;
}
.experts-location span {
  display: inline-block;
  vertical-align: middle;
}
.wind-location {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 12;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
}
.location-content {
  background: #fff;
  position: absolute;
  width: 50%;
  left: 25%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  text-align: center;
  font-size: 1.5vw;
  padding: 30px 30px;
  border-radius: 10px;
}
.location-item {
  display: inline-block;
  border: 1px solid #3c9bff;
  width: 25%;
  line-height: 80px;
  border-radius: 10px;
  color: #3c9bff;
}
.location-head {
  margin-bottom: 30px;
}
.location-bottom > div:nth-child(1) {
  margin-right: 10%;
}
.page {
  position: fixed;
  left: 0px;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  z-index: 99;
}
.content {
  width: 100%;
  height: 68%;
  background-color: #fff;
  padding-bottom: 20px;
  margin-left: 200px;
  padding-left: 0.2rem;
}
.content .certain {
  padding-right: 30px;
  height: 10%;
  text-align: right;
  line-height: 40px;
  font-family: PingFangSC-Regular;
  font-size: 17px;
  color: #666;
}
.content .box {
  padding: 0 15px;
  height: 10%;
  border-bottom: 1px solid #eee;
  border-top: 1px solid #eee;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  line-height: 40px;
  font-size: 1.5vw;
}
.box .box_content {
  float: left;
  margin-right: 20px;
}
.content .box div.active {
  color: #3c9bff;
  border-bottom: 1px solid #3c9bff;
}
.content .box .active {
  color: #3c9bff;
  border-bottom: 1px solid #3c9bff;
}
.content .address {
  padding-left: 15px;
  height: 45px;
  line-height: 45px;
  font-family: PingFangSC-Regular;
  font-size: 1.5vw;
  color: #333;
}
.content .address.on {
  color: #3C9BFF;
}
.content .scroll {
  height: 90%;
  overflow: scroll;
}
.content .scroll div.active {
  color: #3c9bff;
}
.content .scroll .active {
  color: #3c9bff;
}
.nationwide {
  display: inline-block;
  padding: 0 2%;
  border-radius: 3px;
}
.nationwide.on {
  background: #3C9BFF;
  color: #fff !important;
}
/*时间相关*/
.reservation-time-left {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  border-bottom: 1px solid #eee;
  height: 0.9rem;
}
.reservation-time-left .select {
    display: inline-block;
    width: 20%;
    height: 0.6rem;
    border: 1px solid #999;
    border-radius: 0.5rem;
    padding: 0 0.25rem;
    float: right;
    margin-right: 0.4rem;
    margin-top: 0.15rem;
}
.reservation-time-left .select input {
      border: none;
      line-height: 0.6rem;
      width: calc(100% - 0.3rem);
      font-size: 0.28rem;
}
.reservation-time-left .select img {
      width: 0.3rem;
      vertical-align: middle;
}
/*.reservation-time-left:after{
		content: '';
        width: 0px;
        height: 100%;
        display: inline-block;
        vertical-align: middle;	
        }*/
.time-left-item {
  display: inline-block;
  text-align: center;
  color: #999;
  padding: 0 2%;
  position: relative;
  height: .9rem;
}
.time-left-item span {
  display: block;
}
.time-left-item > span:nth-child(1) {
  font-size: 1.5vw;
}
.time-left-item > span:nth-child(2) {
  font-size: 1.8vw;
  padding-bottom: 3px;
}
.time-left-item .line {
  position: absolute;
  bottom: 0;
  width: 30%;
  background: #3c9bff;
  display: inline-block;
  height: 2px;
  left: 35%;
  display: none;
}
.time-left-item.on span {
  color: #3c9bff;
}
.time-left-item.on .line {
  display: block;
}
